import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import  Img  from 'gatsby-image'
import { Row, Col } from 'react-bootstrap'

import SocialList from '../components/commons/socialLinksList'
import Layout from '../components/commons/layout'
import SEO from '../components/commons/seo'

const IndexPage = () => (
    <StaticQuery query={graphql`
      {
        profilePhoto: file(relativePath: {eq: "Home/"}){
          childImageSharp{
              fluid(maxWidth: 2080, quality: 100){
                  aspectRatio
                  sizes
                  src
                  srcSet
                  srcWebp
                  srcSetWebp
              }
            }
        }
      }
    `} render={(data) => 
    {
      var photo = data.profilePhoto.childImageSharp;
      var height = window.screen.height;
    return (
    <Layout>
      <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
        <Row>
          <Col lg={{span:8, offset:2}} xs="12" md="12">
            <Row>
              <Col>
                {console.log(data)}
                {/* style={{borderRadius:`10px`, clipPath:`polygon(40% 0, 0 0, 0% 100%, 80% 100%)`}} */}
                {height < 1080 ?
                  <Img fluid={photo.fluid} fadeIn={true} objectFit="contain" objectPosition="50% 50%" alt="thejengo"  style={{borderRadius:`10px`, height:``+height/3.5+`px`}}/>
                  :
                  <Img fluid={photo.fluid} fadeIn={true} objectFit="contain" objectPosition="50% 50%" alt="thejengo"  style={{borderRadius:`10px`, height:``+height/2+`px`}}/>
                }
              </Col>
            </Row>
            <Row> 
              <Col lg="12" xs="12" md="12">
                <h5 align="center">When I'm Cengiz.</h5>
                <p>
                I love any kind of code and music. They are like my nature. They are so charmy and amazing. I'm losing myself when I'm coding or playing an instrument. Thats the exact time when I'm Cengiz.
                </p>
                <p>
                  Here's my social media accounts you can text or follow to know me closer:
                </p>
                <SocialList size="50px" />
              </Col>
            </Row>
          </Col>
        </Row>
    </Layout>
    )}}
  />
)

export default IndexPage
